@use "base/variables" as v;
@use "base/mixins" as m;

.reestablecer {
    margin-top: 5rem;
    .uptask {
        @include m.textoDegradado(v.$indigo, v.$cyan);
    }

    input[type="submit"] {
        @include m.boton(v.$indigo, v.$blanco);
    }

    .acciones {
        a {
            color: v.$indigo;
        }
    }
}
